@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");

* {
  margin: 0;
  padding: 0;
}
html {
  font-family: Poppins, "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  line-height: 1.5rem;
}
body {
  min-height: 100vh;
  min-height: 100dvh;
  background-color: transparent;
  color: var(--primary-text-color);
  display: grid;
  grid-template-columns: auto 1fr;
}
#sidebar {
  box-sizing: border-box;
  margin: 32px 0 32px 32px;
  height: calc(100vh - 64px);
  width: 250px;
  padding: 5px 1em;
  background: rgba(var(--surface-color-rgb), 0.1);
  border-radius: 16px;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(4.9px);
  -webkit-backdrop-filter: blur(4.9px);
  border: 2px solid var(--border-color);
  position: sticky;
  top: 0;
  align-self: start;
  transition: 300ms ease-in-out;
  overflow: hidden;
  text-wrap: nowrap;
  display: flex;
  flex-direction: column;
}
#sidebar.close {
  padding: 5px;
  width: 60px;
}
#sidebar ul {
  list-style: none;
  flex: 1;
}

.logo-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.brand-text {
  font-size: 0.9rem;
  font-weight: 600;
  color: var(--primary-text-color);
  opacity: 0.8;
  letter-spacing: 0.5px;
  text-transform: uppercase;
}

.sidebar-separator {
  width: 80%;
  height: 1px;
  border: none;
  background: linear-gradient(
    90deg,
    transparent,
    var(--muted-text-color),
    transparent
  );
  margin: 1em auto;
  opacity: 0.6;
}

.sidebar-bottom {
  margin-top: auto;
}

.sidebar-toggle {
  background: none;
  border: none;
  cursor: pointer;
  padding: 0.5em;
  border-radius: 0.5em;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: background-color 0.3s ease;
  min-width: 40px;
  height: 40px;
  width: auto; /* Override the 100% width from general selector */
}

.sidebar-toggle:hover {
  background-color: var(--hover-color);
}

.sidebar-toggle svg {
  fill: var(--primary-text-color);
  transition: fill 0.3s ease;
}

.logo {
  height: 80px;
  width: auto;
  max-width: 100%;
  flex-shrink: 0;
  display: block;
  margin: 0 auto;
  object-fit: contain;
}

/* Text logo styling (for when span is used instead of img) */
.logo.text-logo {
  font-size: 1.25rem;
  font-weight: bold;
  background: linear-gradient(
    135deg,
    var(--accent-color, var(--primary-color)),
    var(--error-color)
  );
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  text-align: center;
  letter-spacing: -0.015em;
  line-height: 1.2;
  border-radius: 0.5em;
  padding: 0.85em;
  text-decoration: none;
  color: transparent;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1em;
  transition: background 0.3s ease, transform 0.3s ease;
  position: relative;
  overflow: hidden;
  width: 100%;
}
.logo.text-logo::before {
  content: "";
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    90deg,
    transparent,
    rgba(255, 255, 255, 0.1),
    transparent
  );
  transition: left 0.5s ease;
  z-index: 1;
}
.logo.text-logo:hover::before {
  left: 100%;
}
.logo.text-logo:hover {
  background-color: var(--hover-color);
  transform: translateY(-1px);
}

#sidebar ul li.active a {
  color: var(--accent-color);
}
#sidebar ul li.active a svg {
  fill: var(--accent-color);
}
#sidebar a,
#sidebar .dropdown-btn,
#sidebar .sidebar-toggle,
#sidebar .theme-toggle {
  border-radius: 0.5em;
  padding: 0.85em;
  text-decoration: none;
  color: var(--primary-text-color);
  display: flex;
  align-items: center;
  gap: 1em;
}
.dropdown-btn,
.sidebar-toggle,
.theme-toggle {
  width: 100%;
  text-align: left;
  background: none;
  border: none;
  font: inherit;
  cursor: pointer;
}
#sidebar svg {
  flex-shrink: 0;
  fill: var(--primary-text-color);
}
#sidebar a span,
#sidebar .dropdown-btn span,
#sidebar .theme-toggle span {
  flex-grow: 1;
}
#sidebar a:hover,
#sidebar .dropdown-btn:hover,
#sidebar .sidebar-toggle:hover,
#sidebar .theme-toggle:hover {
  background-color: var(--hover-color);
}
#sidebar .sub-menu {
  display: grid;
  grid-template-rows: 0fr;
  transition: 300ms ease-in-out;
}
#sidebar .sub-menu > div {
  overflow: hidden;
}
#sidebar .sub-menu.show {
  grid-template-rows: 1fr;
}
.dropdown-btn svg {
  transition: 200ms ease;
}
.rotate svg:last-child {
  rotate: 180deg;
}
#sidebar .sub-menu a {
  padding-left: 2em;
}
#toggle-btn {
  margin-left: auto;
  padding: 1em;
  border: none;
  border-radius: 0.5em;
  background: none;
  cursor: pointer;
}
#toggle-btn svg {
  transition: rotate 150ms ease;
}
#toggle-btn:hover {
  background-color: var(--hover-color);
}
main {
  padding: min(30px, 7%);
}
main p {
  color: var(--secondary-text-color);
  margin-top: 5px;
  margin-bottom: 15px;
}
.container {
  border: 1px solid var(--border-color);
  border-radius: 1em;
  margin-bottom: 20px;
  padding: min(3em, 15%);
}
.container h2,
.container p {
  margin-top: 1em;
}
@media (max-width: 800px) {
  body {
    grid-template-columns: 1fr;
  }
  main {
    padding: 2em 1em 60px 1em;
  }
  .container {
    border: none;
    padding: 0;
  }
  #sidebar {
    height: 60px;
    width: calc(100% - 60px);
    border-right: none;
    border-top: 1px solid var(--border-color);
    padding: 0;
    position: fixed;
    top: unset;
    bottom: 0;
  }
  #sidebar > ul {
    padding: 0;
    display: grid;
    grid-auto-columns: 60px;
    grid-auto-flow: column;
    align-items: center;
    justify-content: space-around;
    overflow-x: scroll;
  }
  #sidebar ul li {
    height: 100%;
  }
  #sidebar ul a,
  #sidebar ul .dropdown-btn,
  #sidebar ul .theme-toggle {
    width: 60px;
    height: 60px;
    padding: 0;
    border-radius: 0;
    justify-content: center;
  }
  #sidebar ul li span,
  #sidebar ul li:first-child,
  .dropdown-btn svg:last-child,
  .logo-container,
  .brand-text,
  .sidebar-separator,
  .sidebar-bottom {
    display: none;
  }

  /* Mobile-specific styles for sidebar toggle and logo */
  .sidebar-toggle {
    width: 60px;
    height: 60px;
    padding: 0;
    border-radius: 0;
    min-width: unset;
  }

  .logo {
    display: none;
  }
  #sidebar ul li .sub-menu.show {
    position: fixed;
    bottom: 60px;
    left: 0;
    box-sizing: border-box;
    height: 60px;
    width: calc(100% - 60px);
    background-color: var(--hover-color);
    border-top: 1px solid var(--border-color);
    display: flex;
    justify-content: center;
  }
  #sidebar ul li .sub-menu.show > div {
    overflow-x: auto;
  }
  #sidebar ul li .sub-menu.show li {
    display: inline-flex;
  }
  #sidebar ul li .sub-menu.show a {
    box-sizing: border-box;
    padding: 1em;
    width: calc(100% - 60px);
    justify-content: center;
  }
}
